import React, { Component } from "react";
import { withRouter } from "react-router-dom";

import FannstNews from "../../../images/fannst-news.png";

import "./NavTop.styles.scss";

class NavTop extends Component {
    constructor(props) {
        super(props);

        this.state = {
            autocomplete: []
        };
    }

    getAutocomplete = () => {
        let { search_input } = this.refs;

        let xml = new XMLHttpRequest();
        xml.open("POST", "https://fannst.nl/rest/news/autocomplete");
        xml.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
        xml.send("params=" + search_input.value + "&country=nl");
        xml.onreadystatechange = function () {
            if(xml.status === 200 && xml.readyState === 4) {
                let response = JSON.parse(xml.responseText);

                if(response.status) {
                    this.setState({
                        autocomplete: response.results
                    });
                }
            }
        }.bind(this);
    };

    render () {
        let { autocomplete } = this.state;

        return (
            <div className="NavTop">
            <span>
                <span>
                    <button>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M0 0h24v24H0z" fill="none"/><path d="M3 18h18v-2H3v2zm0-5h18v-2H3v2zm0-7v2h18V6H3z"/></svg>
                    </button>
                </span>
                <span>
                    <img alt="Fannst News" src={FannstNews} />
                </span>
            </span>
                <span>
                <span>
                    <button>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/><path d="M0 0h24v24H0z" fill="none"/></svg>
                    </button>
                </span>
                <span>
                    <input onKeyDown={(e) => {
                        if(e.keyCode === 13) {
                            this.props.history.push("/search/" + e.target.value);
                        } else {
                            this.getAutocomplete();
                        }
                    }} ref="search_input" placeholder="Search news articles" />
                    <div className="NavTop__SearchAutocomplete">
                        <ul>
                            {autocomplete.map(item => {
                                return (
                                    <li>
                                        <button onClick={() => {
                                            this.props.history.push("/search/" + item.params)
                                        }}>{item.params}</button>
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                </span>
                <span>
                    <button>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M7 10l5 5 5-5z"/><path d="M0 0h24v24H0z" fill="none"/></svg>
                    </button>
                </span>
            </span>
            <span>
                <span>
                    <button>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M4 8h4V4H4v4zm6 12h4v-4h-4v4zm-6 0h4v-4H4v4zm0-6h4v-4H4v4zm6 0h4v-4h-4v4zm6-10v4h4V4h-4zm-6 4h4V4h-4v4zm6 6h4v-4h-4v4zm0 6h4v-4h-4v4z"/><path d="M0 0h24v24H0z" fill="none"/></svg>
                    </button>
                </span>
                <span>
                    <a href="https://auth.fannst.nl" target="_blank">Inloggen</a>
                </span>
            </span>
            </div>
        );
    }
}
export default withRouter(NavTop);