import React from "react";

import ArticleDefaultImage from "../../images/default-article-image.png";

import "./ArticleList.styles.scss";

const ArticleList = ({ articles }) => {
    if(!articles) {
        return null;
    } else {
        return (<ul className="ArticleList">
            {articles.map(article => {
                let diff = (new Date().getTime() - new Date(article.pubDate).getTime()) / 1000;
                diff /= (60 * 60);
                diff = Math.abs(Math.round(diff));

                return (<li className="ArticleList__Article">
                <span>
                    <a target="_blank" href={article.guid}>
                        <h3>{article.title}</h3>
                        <div>
                            <span>{article.pubName}</span>
                            <span> ● </span>
                            <span>{diff} uur geleden</span>
                        </div>
                        <small>{article.contentSnippet.length > 120 ? article.contentSnippet.substring(0, 120) + " ..." : article.contentSnippet}</small>
                    </a>
                </span>
                    <span>
                    <img src={article.image === "default" ? ArticleDefaultImage : article.image} />
                </span>
                </li>);
            })}
        </ul>);
    }
};

export default ArticleList;
