import React from "react";
import { NavLink } from "react-router-dom";

import "./NavSideList.styles.scss";

const NavSideList = ({items}) => {
    return (
        <div className="NavSideList">
            <ul>
                {items.map(item => {
                    return (<li>
                        <NavLink className="NavSideList__Item-InActive" activeClassName="NavSideList__Item-Active" to={item.to}>
                            <span>
                                {item.icon}
                            </span>
                            <span>
                                {item.title}
                            </span>
                        </NavLink>
                    </li>);
                })}
            </ul>
        </div>
    );
};

export default NavSideList;