import React, { Component } from 'react';

import ArticleList from "../src/ArticleList/ArticleList.component";
import InlineLayout from "../src/InlineLayout/InlineLayout.component";
import CategoryHead from "../src/CategoryHead/CategoryHead.component";

class Business extends Component {
    constructor() {
        super();

        this.state = {
            articles: []
        };

        this.componentDidMount = () => {
            this.getLatestArticles();
        }
    }

    getLatestArticles = () => {
        let xml = new XMLHttpRequest();
        xml.open("POST", "https://fannst.nl/rest/news/get-latest");
        xml.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
        xml.send("category=business&country=nl");
        xml.onreadystatechange = function () {
            if (xml.readyState === 4 && xml.status === 200) {
                let response = JSON.parse(xml.responseText);
                this.setState({
                    articles: response.articles
                });
            }
        }.bind(this);
    };

    render() {
        let { articles } = this.state;

        return (
            <div className="Business">
                <InlineLayout>
                    <span>
                        <CategoryHead title="Zakelijk" icon={<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M0 0h24v24H0z" fill="none"/><path d="M12 7V3H2v18h20V7H12zM6 19H4v-2h2v2zm0-4H4v-2h2v2zm0-4H4V9h2v2zm0-4H4V5h2v2zm4 12H8v-2h2v2zm0-4H8v-2h2v2zm0-4H8V9h2v2zm0-4H8V5h2v2zm10 12h-8v-2h2v-2h-2v-2h2v-2h-2V9h8v10zm-2-8h-2v2h2v-2zm0 4h-2v2h2v-2z"/></svg>} />
                        <ArticleList articles={articles} />
                    </span>
                </InlineLayout>
            </div>
        );
    }
}

export default Business;
