import React, { Component } from "react";
import cookie from "react-cookies";

import ExplosionIcon from "../../images/explosion-icon.png";

import "./TerrorCounter.styles.scss";

class TerrorCounter extends Component {
    constructor () {
        super();

        this.state = {
            weather: undefined
        };

        this.componentDidMount = () => {
        };
    }

    render () {
        return (<div className="TerrorCounter">
            <strong>Terreur teller</strong>
            <hr />
            <div className="TerrorCounter__recent">
                <span>
                    <text>Laatste aanslag</text>
                    <br />
                    <text>5 Dagen geleden</text>
                </span>
                <span>
                    <img alt="Aanslag" src={ExplosionIcon} />
                </span>
            </div>
            <hr />
            <div className="TerrorCounter__count">
                <table>
                    <tr>
                        <th></th>
                        <th>Islamitisch</th>
                        <th>Christelijk</th>
                        <th>Anders</th>
                    </tr>
                    <tr>
                        <th>Totale aanslagen: </th>
                        <td>123</td>
                        <td>12</td>
                        <td>4</td>
                    </tr>
                    <tr>
                        <th>Totale slachtoffers: </th>
                        <td>23.234</td>
                        <td>400</td>
                        <td>200</td>
                    </tr>
                </table>
            </div>
            <hr />
            <div className="TerrorCounter__bottom">
                <span>Aan het tellen sinds: 9/7/2019</span>
            </div>
        </div>);
    }
}

export default TerrorCounter;