import React, { Component } from 'react';

import ArticleList from "../src/ArticleList/ArticleList.component";
import Weather from "../src/Weather/Weather.component";
import InlineLayout from "../src/InlineLayout/InlineLayout.component";
import TerrorCounter from "../src/TerrorCounter/TerrorCounter.component";

class Frontpage extends Component {
    constructor() {
        super();

        this.state = {
            articles: []
        };

        this.componentDidMount = () => {
            this.getLatestArticles();
        }
    }

    getLatestArticles = () => {
        let xml = new XMLHttpRequest();
        xml.open("POST", "https://fannst.nl/rest/news/get-latest");
        xml.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
        xml.send("category=normal&country=nl");
        xml.onreadystatechange = function () {
            if (xml.readyState === 4 && xml.status === 200) {
                let response = JSON.parse(xml.responseText);
                this.setState({
                    articles: response.articles
                });
            }
        }.bind(this);
    };

    render() {
        let { articles } = this.state;
        let { onTemperatureUnitChange } = this.props;

        return (
            <div className="Frontpage">
                <InlineLayout>
                    <span>
                        <ArticleList articles={articles} />
                    </span>
                    <span>
                        <Weather onUnitChange={(unit) => onTemperatureUnitChange(unit)} />
                        <TerrorCounter />
                    </span>
                </InlineLayout>
            </div>
        );
    }
}

export default Frontpage;
