import React, { Component } from 'react';

import ArticleList from "../src/ArticleList/ArticleList.component";
import InlineLayout from "../src/InlineLayout/InlineLayout.component";
import CategoryHead from "../src/CategoryHead/CategoryHead.component";

class Search extends Component {
    constructor(props) {
        super(props);

        this.state = {
            articles: [],
            params: props.match.params.params
        };

        this.componentDidMount = () => {
            this.getLatestArticles();
        };

        this.props.history.listen(function (location, action) {
            this.setState({
                params: location.pathname.split("/").pop()
            }, function() {
                this.getLatestArticles();
            })
        }.bind(this));
    }

    getLatestArticles = () => {
        let { params } = this.state;

        console.log(params);

        let xml = new XMLHttpRequest();
        xml.open("POST", "https://fannst.nl/rest/news/search");
        xml.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
        xml.send("params=" + params + "&country=nl");
        xml.onreadystatechange = function () {
            if (xml.readyState === 4 && xml.status === 200) {
                let response = JSON.parse(xml.responseText);
                this.setState({
                    articles: response.articles
                });
            }
        }.bind(this);
    };

    render() {
        let { articles } = this.state;

        return (
            <div className="Search">
                <InlineLayout>
                    <span>
                        <ArticleList articles={articles} />
                    </span>
                </InlineLayout>
            </div>
        );
    }
}

export default Search;
