import React, { Component } from 'react';

import ArticleList from "../src/ArticleList/ArticleList.component";
import InlineLayout from "../src/InlineLayout/InlineLayout.component";
import CategoryHead from "../src/CategoryHead/CategoryHead.component";

class Worldwide extends Component {
    constructor() {
        super();

        this.state = {
            articles: []
        };

        this.componentDidMount = () => {
            this.getLatestArticles();
        }
    }

    getLatestArticles = () => {
        let xml = new XMLHttpRequest();
        xml.open("POST", "https://fannst.nl/rest/news/get-latest");
        xml.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
        xml.send("category=normal&country=uk");
        xml.onreadystatechange = function () {
            if (xml.readyState === 4 && xml.status === 200) {
                let response = JSON.parse(xml.responseText);
                this.setState({
                    articles: response.articles
                });
            }
        }.bind(this);
    };

    render() {
        let { articles } = this.state;

        return (
            <div className="Business">
                <InlineLayout>
                    <span>
                        <CategoryHead title="Worldwide" icon={<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M0 0h24v24H0z" fill="none"/><path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zM5 4.99h3C8 6.65 6.66 8 5 8V4.99zM5 12v-2c2.76 0 5-2.25 5-5.01h2C12 8.86 8.87 12 5 12zm0 6l3.5-4.5 2.5 3.01L14.5 12l4.5 6H5z"/></svg>} />
                        <ArticleList articles={articles} />
                    </span>
                </InlineLayout>
            </div>
        );
    }
}

export default Worldwide;
