import React, { Component } from "react";
import { Route } from "react-router-dom";

import "./App.scss";

import Frontpage from "./pages/Frontpage.component";
import Business from "./pages/Business.component"
import Sports from "./pages/Sports.component";
import Science from "./pages/Science.component";
import Search from "./pages/Search.component";
import BottomNotification from "./src/BottomNotification/BottomNotification.component";
import Worldwide from "./pages/Worldwide.component";

import NavTop from "./src/Nav/NavTop/NavTop.component";
import NavSide from "./src/Nav/NavSide/NavSide.component";

class App extends Component {
    constructor () {
        super();
    }

    render () {
        return (<div className="App">
            <NavTop />
            <NavSide out={true} />
            <BottomNotification ref="notification" />
            <div className="App__Container">
                <Route exact path="/frontpage" component={() => {
                    return (<Frontpage onTemperatureUnitChange={(unit) => {
                        this.refs.notification.showNotification(2400, "Changed local temperature unit to: " + unit + ".");
                    }} />)
                }} />
                <Route exact path="/news/business" component={Business} />
                <Route exact path="/news/sports" component={Sports} />
                <Route exact path="/news/science" component={Science} />
                <Route exact path="/search/:params" component={Search} />
                <Route exact path="/news/world" component={Worldwide} />
            </div>
        </div>)
    }
}

export default App;