import React, { Component } from "react";
import classnames from "classnames";

import "./BottomNotification.styles.scss";

class BottomNotification extends Component {
    constructor(props, ref) {
        super(props, ref);

        this.state = {
            show: false,
            text: ""
        }
    }

    showNotification = (ms, text) => {
        this.setState({
            show: true,
            text: text
        }, function () {
            setTimeout(function () {
                this.setState({
                    show: false,
                    text: ""
                })
            }.bind(this), ms)
        }.bind(this));
    };

    render() {
        let { text, show } = this.state;

        let classes = classnames({
            BottomNotification: true,
            BottomNotification__active: show,
            BottomNotification__inactive: !show
        });

        return (
            <div className={classes}>
                {text}
            </div>
        );
    }
}

export default BottomNotification;