import React, { Component } from "react";
import cookie from "react-cookies";

import "./Weather.styles.scss";

let daysNL = ['Zondag', 'Maandag', 'Dinsdag', 'Woensdag', 'Donderdag', 'Vrijdag', 'Zaterdag'];

class Weather extends Component {
    constructor () {
        super();

        this.state = {
            weather: undefined
        };

        this.componentDidMount = () => {
            this.getWeather();
        };
    }

    getWeather = () => {
      let xml = new XMLHttpRequest();
      xml.open("POST", "https://fannst.nl/rest/weather/get");
      xml.setRequestHeader("Content-Type", "application/x-form-urlencoded");
      xml.send();
      xml.onreadystatechange = function () {
          if (xml.readyState === 4 && xml.status === 200) {
             let response = JSON.parse(xml.responseText);
             this.setState({
                 weather: response.weather
             });
          }
      }.bind(this);
    };

    render () {
        let { weather } = this.state;
        let temp_unit = cookie.load("temp_unit");
        let { onUnitChange } = this.props;

        if (!weather) {
           return "";
        } else {
            return (<div className="Weather">
                <strong>{weather.location.name}</strong>
                <hr />
                <div className="Weather__current">
                    <span>
                        <text>{weather.current.condition.text}</text>
                        <br />
                        <text>{temp_unit === "c" ? weather.current.temp_c + " °c" : weather.current.temp_f + " °f"}</text>
                    </span>
                    <span>
                        <img src={weather.current.condition.icon} />
                    </span>
                </div>
                <div className="Weather__tomorrow">
                    {weather.forecast.forecastday.map(forecast => {
                        return (<span>
                            <text>{daysNL[new Date(forecast.date).getDay()]}</text>
                            <br />
                            <img src={forecast.day.condition.icon} />
                            <br />
                            <text>{ temp_unit === "c" ? forecast.day.maxtemp_c + " °c" : forecast.day.maxtemp_f + " °f"}</text>
                            <br />
                            <text>{ temp_unit === "c" ? forecast.day.mintemp_c + " °c" : forecast.day.mintemp_f + " °f"}</text>
                        </span>);
                    })}
                </div>
                <hr />
                <div className="Weather__bottom">
                    <span>
                        <ul>
                            <li>
                                <button onClick={(e) => {
                                    cookie.save("temp_unit", "c");
                                    onUnitChange("Celsius");
                                    this.getWeather();
                                }}>C</button>
                            </li>
                            <li>
                                <button onClick={(e) => {
                                    cookie.save("temp_unit", "f");
                                    onUnitChange("Fahrenheit");
                                    this.getWeather();
                                }}>F</button>
                            </li>
                        </ul>
                    </span>
                    <span>
                        <a target="_blank" href="https://www.apixu.com/weather/">Meer op Apixu.com</a>
                    </span>
                </div>
            </div>);
        }
    }
}

export default Weather;